.editor-hovering-menu {
  display: flex;
  border-radius: 6px;
  background-color: lumx-theme-color-variant('dark', 'N');
  padding: 6px;
  color: lumx-theme-color-variant('light', 'N');
}

.editor-menu {
  background-color: transparent;

  &__content {
    margin-top: 8px;
    margin-bottom: 8px;
    box-shadow: 0 2px 8px 0 lumx-theme-color-variant('dark', 'L4');
  }
}

