@import "~@lumx/react/scss/_core-lumapps";

*:focus:not(.focus-visible) {
  outline: none;
}

html,
body {
  height: 100%;
}

#root {
  height: 100vh;
}

.card {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 342px;
  background-color: white;

  header {
    padding: 24px;
  }

  .editor {
    flex: 1;
  }

  footer {
    display: flex;
    justify-content: flex-end;
    padding: 14px;
  }
}

@media only screen and (min-width: 768px) {
  // For desktop
  #root {
    padding: 10px;
  }

  .card {
    margin: auto;
    box-shadow: 0 1px 4px 0 lumx-theme-color-variant('dark', 'L3');
    border-radius: 4px;
    max-width: 744px;
  }
}

@import "editor/index";
