.wrex-content-figure-options {

  &__content {
    margin: 8px;
    flex-direction: column;
    padding: 14px !important;
  }

  &__header {
    display: flex;
    align-items: center;
  }

  &__title {
    flex: 1;
    font-weight: bold;
    font-size: 20px;
  }

  &__subtitle {
    margin-top: 19px;
    font-weight: bold;
    font-size: 14px;
  }

  &__button-choices {
    display: flex;
    margin-top: 10px;
    white-space: nowrap;

    > * {
      flex: 1;

      &:not(:last-child) {
        margin-right: 2px;
      }
    }
  }
}